@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

.form {
  display: flex;
  justify-content: center;
  background-color: rgb(233, 235, 237);
  width: 600px;
  margin-left: 35%;
  border-radius: 9px;
  margin-top: 220px;
  padding: 40px;
}

.form h3 {
  font-size: 2.3rem;
  padding-left: 140px;
  padding-bottom: 20px;
  color: rgb(66, 67, 67);
  font-weight: bold;
}
.form input {
  font-size: 16px;
  border-radius: 5px;
  height: 35px;
}
.form label {
  font-size: 16px;
  color: rgb(66, 67, 67);
  font-weight: bold;
}

input[type="text"] {
  width: 500px;
  padding: 8px;
}

.btn {
  font-size: 17px;
  border-radius: 6px;
  border: none;
  background-color: rgb(143, 143, 143);
  font-weight: bold;
  padding: 10px 25px;
}

.btn:hover {
  background-color: rgb(63, 63, 63);
  color: white;
}
