.title {
  font-size: 34px;
  padding: 10px;
  font-weight: bold;
  color: rgb(70, 69, 69);
  background-image: linear-gradient(
    to top right,
    rgb(179, 179, 179),
    rgb(255, 255, 255)
  );
}

.chart {
  padding: 20px 100px;
}

.chart-container {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin: 25px 200px;
}
