@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

.header {
  background-color: rgb(52, 50, 50);
  padding: 10px;
  color: white;
  height: 60px;
  top: 0;
  width: 100%;
}

.home {
  margin-top: 2px;
  margin-left: 25px;
  display: block;
}
.home ul {
  list-style-type: none;
  font-size: 22px;
  display: flex;
}

.link {
  color: white;
  text-decoration: none;
}
.home li {
  margin-left: 25px;
}

.link:hover {
  color: rgb(200, 198, 198);
  cursor: pointer;
}

.name {
  color: white;
  float: right;
  font-size: 14px;
  margin-top: -35px;
}

.logout {
  font-size: 14px;
  border-radius: 6px;
  border: none;
  background-color: rgb(143, 143, 143);
  font-weight: bold;
  padding: 10px 20px;
  margin-left: 30px;
  margin-top: -3px;
}

.logout:hover {
  background-color: rgb(88, 87, 87);
  color: white;
}
